<template>
  <div>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-date-picker
            v-model="date"
            full-width
            locale="es-es"
            :min="minYear"
            :max="maxYear"
            @change="getEuro(date)"
          ></v-date-picker>
        </v-card>
        <v-card color="error" dark>
          <v-card-text class="display-1 text-center">{{value}} €</v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";
// Librería momentjs.com
const moment = require("moment");
import { mapMutations } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      date: moment().format("YYYY-MM-DD"),
      minYear: "1984",
      maxYear: moment().format("YYYY-MM-DD"),
      value: null
    };
  },
  methods: {
    ...mapMutations(["showLoad", "hideLoad"]),

    async getEuro(dia) {
      let arrayFecha = dia.split(["-"]);
      let ddmmyy = `${arrayFecha[2]}-${arrayFecha[1]}-${arrayFecha[0]}`;

      try {
        this.showLoad({ title: "Accediendo información", color: "accent" });

        let datos = await axios.get(`https://mindicador.cl/api/euro/${ddmmyy}`);
        // Consultamos si existe algún valor para ejecutar la línea o pasar al error
        if (datos.data.serie.length > 0) {
          // Estructura de consulta a la api
          this.value = await datos.data.serie[0].valor;
        } else {
          this.value = "No hay resultados";
        }
      } catch (error) {
        console.log(error);
      } finally {
          this.hideLoad()
      }
    }
  },
  created() {
    let fecha = moment().format("YYYY-MM-DD");
    this.getEuro(fecha);
  }
};
</script>